.check {
  @include list-reducer();
}

.check__item {
  margin-top: 32px;
  width: 200px;
}

.check__label {
  @include title(17px, 1.06);

  margin-top: 45px;
  padding-left: 24px;
  text-transform: none;
}

.check__box {
  position: relative;

  display: inline-block;
  vertical-align: middle;
  margin-top: -12px;
  width: 40px;
  height: 40px;

  border: 2px solid black;

  &:before {
    content: "";

    position: absolute;

    top: 50%;
    left: 50%;

    margin-left: -10px;
    margin-top: -6px;
    width: 20px;
    height: 10px;

    border-left: 2px solid black;
    border-bottom: 2px solid black;

    transform: rotate(-45deg);
  }
}

@media (min-width: $tablet-width) {
  .check {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 18px;
  }
}
