.week-product {
  margin-top: 30px;
  padding-bottom: 36px;
}

.week-product__wrapper {
  @include wrapper(320px, 30px);
  margin: 0 auto;
}

.week-product__info {
  margin: 0;
  margin-top: 30px;
}

.week-product__title {
  @include title(25px, 1.2);
}

.week-product__label {
  font-size: 17px;
  font-weight: bold;
  font-style: normal;
  line-height: 1.18;
  color: $green;
  text-transform: uppercase;
}

.week-product__price {
  font-size: 17px;
  font-weight: bold;
  font-style: normal;
  line-height: 1.41;
}

.week-product__picture {
  margin: 0;
  padding: 0;
  width: 260px;

  background-color: $gray;
}

.week-product__link {
  width: 100%;
}

