.radiobutton {
  @include title(17px, 1.06);
  position: relative;

  margin-top: 25px;
  padding-left: calc(22px + 40px);
  text-transform: none;
}

.radiobutton__icon {
  position: absolute;
  top: 0;
  left: 0;

  display: inline-block;
  margin-top: -10px;
  width: 40px;
  height: 40px;

  border: solid 2px #444444;
  border-radius: 50%;

  &:before {
    content: "";

    position: absolute;
    top: 50%;
    left: 50%;

    margin-top: -8px;
    margin-left: -8px;
    width: 16px;
    height: 16px;

    background-color: $green;
    border-radius: 50%;
  }
}
