.form__item {
  @include wrapper(320px, 30px);

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin: 0 auto;
  margin-top: 30px;

  border: none;
  outline: 2px solid red;

  &--info {
    font-size: 13px;
    line-height: 1.85;
  }
}

.form__title {
  position: relative;

  margin-top: 23px;
  width: 100%;

  font-size: 17px;
  line-height: 0.94;
  color: $darkgreen;
  text-transform: uppercase;

  &:before {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;

    display: block;
    width: 198px;

    border-bottom: 1px solid $green;
  }

  &--mail:before {
    width: 178px;
  }
}

.form__text {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 0;

  font-size: 17px;
  font-weight: bold;
  line-height: 1.06;
}

.form__username {
  margin-top: 24px;
}

.form__field {
  width: 100%;
  padding: 17px 0;
  border: none;
  border-bottom: 2px solid #000000;

  &--text {
    margin-top: 23px;
    padding: 17px;
    height: 142px;

    border: 2px solid #000000;
  }
}

.form__field::placeholder {
  font-size: 17px;
  line-height: 1.06;
  opacity: 0.3;
}

.form__warning {
  margin: 0;
  margin-top: 18px;

  font-size: 14px;
  line-height: 1.14;
}

.form__button {
  margin-top: 30px;
}

@media (min-width: $tablet-width) {
  .form__wrapper {
    &--gray {
      background-color: $gray;
    }

    &:first-of-type {
      margin-top: 0;
    }
  }

  .form__item {
    position: relative;

    padding: 50px;
    padding-left: calc(50px + 165px);
    margin-top: 0;
    width: $tablet-width;

    &--color {
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
    }

    &--info {
      font-size: 17px;
      line-height: 1.76;
      padding-left: 50px;
    }

    &--submition {
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
      padding-left: 50px;
    }
  }

  .form__title {
    position: absolute;
    top: 0;
    left: 0;

    margin: 0;
    padding: 0;

    &::before {
      display: none;
    }
  }

  .form__text {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    width: 100%;
    margin-top: 25px;
  }

  .form__contact {
    margin-top: 25px;
  }

  .form__field {

    &--name {
      flex-grow: 1;
      margin-left: 25px;
    }

    &--gray {
      background-color: $gray;
    }
  }

  .form__warning {
    margin: 0;
    width: 300px;
    order: 1;

    text-align: right;
  }

  .form__button {
    margin: 0;
    width: 230px;
  }
}

@media (min-width: $desktop-width) {
  .form__item {
    width: $desktop-width;
    padding: 50px 225px;
  }

  .form__text {
    width: 700px;
    margin: 0 auto;
  }
}
